/* eslint-disable implicit-arrow-linebreak */
/* eslint-disable no-confusing-arrow */
/* eslint-disable multiline-ternary */
import {
  Fragment,
  useState,
  useEffect,
  useContext,
  useMemo,
  useCallback,
  forwardRef,
  lazy,
  Suspense
} from "react"
import { useTranslation } from "react-i18next"
import { AbilityContext } from "@src/utility/context/Can"
import ModalAddEdit from "./modal"
import { neutralizeBack, revivalBack } from "@src/utility/Utils"
import {
  storeName,
  pageTitle,
  serverSideGrid,
  detailFromGrid,
  getApiAfterPut,
  getApiAfterPost,
  getApiAfterDelete,
  getSummarysData,
  getAlwaysGrid,
  getDataList,
  getDataById,
  setDataById,
  deleteData,
  deleteSelectedRowData,
  disabledAction,
  dynamicColumnsFilter,
  setDataList
} from "./store"

import { getDataList as getDataListApp } from "../app/store"
import { getInvoice } from "../../invoices/store"
import { useDispatch, useSelector } from "react-redux"
import ReactPaginate from "react-paginate"
import DataTable from "react-data-table-component"
import debounce from "lodash.debounce"
import {
  ChevronDown,
  Share,
  Printer,
  FileText,
  File,
  Grid
} from "react-feather"

import { getUserData } from "@utils"

import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import toast from "react-hot-toast"
import {
  Row,
  Col,
  Card,
  Input,
  Button,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
  UncontrolledDropdown,
  CardText
} from "reactstrap"

import "@styles/react/libs/react-select/_react-select.scss"
import "@styles/react/libs/tables/react-dataTable-component.scss"
import Swal from "sweetalert2"
import withReactContent from "sweetalert2-react-content"
import { Skeleton } from "@mui/material"
import Moment from "moment"
import GlobalActionsTable from "../../../shared/GlobaActionsTable"

import { getDataList as getDataListPermission } from "../permission/store"
import { NoDataDisplay, Loader } from "@src/shared/CommonComponents"
import Avatar from "@components/avatar"

const MySwal = withReactContent(Swal)

// ** Bootstrap Checkbox Component
const BootstrapCheckbox = forwardRef((props, ref) => (
  <div className="form-check">
    <Input type="checkbox" ref={ref} {...props} />
  </div>
))

// ** Table Header
const CustomHeader = ({
  store,
  toggleSidebar,
  handlePerPage,
  rowsPerPage,
  handleFilter,
  searchTerm,
  selectedRow,
  handleDeleteSelected,
  handleFiltersChange,
  filtersCustomAdd,
  searchString,
  lookupMode,
  lookupMode_fieldName,
  lookupMode_defaultValue,
  lookupMode_onChange,
  lookupMode_isMulti,
  lookupMode_isM2MDefValFromGrid
}) => {
  const { t } = useTranslation()
  const ability = useContext(AbilityContext)

  const dispatch = useDispatch()
  const allStore = useSelector((state) => state)
  const [dynamicFilterComponents, setDynamicFilterComponents] = useState({})
  const [dynamicFilterDatas, setDynamicFilterDatas] = useState({})
  const storeInvoice = useSelector((state) => state.invoices)
  const userData = getUserData()

  const handleChangeInputs = (value, fieldName, isMulti = false) => {
    fieldName = `filter_${fieldName}`
    if (isMulti) {
      if (dynamicFilterDatas[fieldName]) {
        const index = dynamicFilterDatas[fieldName].indexOf(value)
        if (index !== -1) {
          dynamicFilterDatas[fieldName].splice(index, 1)
        } else {
          dynamicFilterDatas[fieldName].push(value)
        }
      } else {
        dynamicFilterDatas[fieldName] = [value]
      }
    } else {
      dynamicFilterDatas[fieldName] = value
    }
    return handleFiltersChange(dynamicFilterDatas[fieldName], fieldName)
  }

  useEffect(() => {
    // DYNAMIC IMPORT COMPONENTS
    let componentsImport = {}
    for (let i = 0; i < dynamicColumnsFilter.length; i++) {
      const fieldName = dynamicColumnsFilter[i]["fieldName"]
      const path_name = dynamicColumnsFilter[i]["fieldName"].replaceAll(
        "id_",
        ""
      )

      dynamicFilterDatas[`filter_${fieldName}`] = null
      const MyComponent = lazy(() => import(`../${path_name}`))
      componentsImport[`${path_name}_comp`] = (
        <MyComponent
          lookupMode={true}
          lookupMode_fieldName={fieldName}
          lookupMode_dropdown={true}
          lookupMode_isMulti={dynamicColumnsFilter[i].isMultiSelection ?? false}
          lookupMode_onChange={handleChangeInputs}
          lookupMode_defaultValue={
            dynamicColumnsFilter[i]?.isM2MDefValFromGrid
              ? allStore[storeName].data.map((x) => x[fieldName])
              : allStore[storeName]?.selectedData
              ? allStore[storeName].selectedData[fieldName]
              : ""
          }
          lookupMode_isM2MDefValFromGrid={
            dynamicColumnsFilter[i]?.isM2MDefValFromGrid ?? false
          }
        />
      )
    }

    setDynamicFilterComponents(componentsImport)
  }, [dispatch, dynamicFilterDatas])

  return (
    <div
      className={`invoice-list-table-header w-100 ms-50 mb-75 ${
        !lookupMode ? "mt-2 me-1" : ""
      }`}
    >
      <Row>
        <Col xl="4" className="d-flex align-items-center p-0">
          {!lookupMode && (
            <div className="d-flex align-items-center w-100">
              <label htmlFor="rows-per-page">{t("Show")}</label>
              <Input
                className="mx-50"
                type="select"
                id="rows-per-page"
                value={rowsPerPage}
                onChange={handlePerPage}
                style={{ width: "5rem" }}
              >
                <option value="10">10</option>
                <option value="25">25</option>
                <option value="50">50</option>
              </Input>
              <label htmlFor="rows-per-page">{t("Entries")}</label>
            </div>
          )}
        </Col>
        <Col
          xl="8"
          className={`d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column p-0 mt-xl-0 mt-1 ${
            !lookupMode ? "pe-xl-1" : ""
          }`}
        >
          {selectedRow?.length > 0 &&
          ability.can("delete", sessionStorage.getItem("current_page_perm")) &&
          !lookupMode ? (
            <div
              className={`d-flex align-items-center mb-sm-0 mb-1 ${
                !lookupMode ? "me-1" : ""
              }`}
            >
              <Button
                className="delete-users"
                color="danger"
                onClick={handleDeleteSelected}
              >
                {t("Delete")} {selectedRow.length} {t(`Selected ${pageTitle}`)}
              </Button>
            </div>
          ) : null}
          <div className={`d-flex align-items-center mb-sm-0 mb-1 me-1`}>
            <label className="mb-0" htmlFor="search-invoice">
              {t("Search")}:
            </label>
            <Input
              id="search-invoice"
              className="ms-50 w-100"
              type="text"
              value={searchTerm}
              onChange={(e) => handleFilter(e.target.value)}
            />
          </div>

          {dynamicColumnsFilter.map((item, index) => {
            const path_name = item.fieldName.replaceAll("id_", "")
            return (
              <div
                key={path_name}
                className={`mt-50 width-300 mt-sm-0 mt-1 ${
                  !lookupMode ? "me-1" : ""
                }`}
              >
                <Suspense fallback={<div>Loading...</div>}>
                  {dynamicFilterComponents[`${path_name}_comp`]}
                </Suspense>
              </div>

              // <Col key={path_name} md={12} xs={12}>
              //   <Label className="form-label" for={item.fieldName}>
              //     {t(item.fieldLabel ?? "")}
              //   </Label>
              //   <Suspense fallback={<div>Loading...</div>}>
              //     <div
              //       className={
              //         !formDatasError[item.fieldName]
              //           ? "dynamicForm_input"
              //           : "dynamicForm_input dynamicForm_input_invalid"
              //       }
              //     >
              //       {dynamicComponents[`${path_name}_comp`]}
              //       {item?.isM2MDefValFromGrid &&
              //         formDatas[item.fieldName] && (
              //           <div
              //             style={{ textAlign: "right" }}
              //             className="text-primary"
              //           >
              //             {formDatas[item.fieldName]?.length}{" "}
              //             {t(item.fieldLabel)} {t("Selected")}
              //           </div>
              //         )}
              //     </div>
              //   </Suspense>
              // </Col>
            )
          })}

          {/* {!filtersCustomAdd["filter_id_permission"] ? (
            <div className="mt-50 width-200 me-1 mt-sm-0 mt-1">
              <Input
                type="select"
                name="select"
                // value={role}
                onChange={(e) =>
                  handleFiltersChange(e.target.value, "filter_id_permission")
                }
              >
                <option value="">
                  {t("Select")} {t("Permission")}
                </option>
                {allStore["apps_permission"].data.map((item, index) => (
                  <option key={item.id} value={item.id}>
                    {item.title}
                  </option>
                ))}
              </Input>
            </div>
          ) : null} */}
          <div className="d-flex align-items-center table-header-actions">
            {ability.can("post", sessionStorage.getItem("current_page_perm")) &&
            !lookupMode ? (
              <Button
                className="add-new-user"
                color="primary"
                onClick={toggleSidebar}
                // onClick={handleAdd}
              >
                {t("Add New")} {t(pageTitle)}
              </Button>
            ) : null}
          </div>
        </Col>
      </Row>
    </div>
  )
}

const List = ({
  filtersCustomAdd,
  searchString,
  lookupMode,
  lookupMode_fieldName,
  lookupMode_defaultValue,
  lookupMode_onChange,
  lookupMode_isMulti,
  lookupMode_isM2MDefValFromGrid
}) => {
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])
  const storeApp = useSelector((state) => state.apps_app)
  const { t } = useTranslation()
  const ability = useContext(AbilityContext)

  const { colors } = useContext(ThemeColors)

  const [sort_dir, setSortDir] = useState("desc")
  const [searchTerm, setSearchTerm] = useState("")
  const [currentPage, setCurrentPage] = useState(1)
  const [sort, setSort] = useState("id")
  const [rowsPerPage, setRowsPerPage] = useState(lookupMode ? 5 : 10)
  const [selectedRows, setSelectedRows] = useState([])

  const [filtersCustom, setFiltersCustom] = useState(filtersCustomAdd ?? {})

  const [toggleCleared, setToggleCleared] = useState(false)

  const [sidebarOpen, setSidebarOpen] = useState(false)
  const [loading, setLoading] = useState(false)
  const [loadingText, setLoadingText] = useState("Please Wait")
  const [showModal, setShowModal] = useState(false)
  const [formState, setFormState] = useState(null)

  const handleEdit = (rowData) => {
    setFormState("edit")
    if (!detailFromGrid) {
      setLoadingText(
        `${t("Loading")} ${t("Detail")} ${t("Data")} ${t(pageTitle)}`
      )
      setLoading(true)
      dispatch(getDataById(rowData.id)).then((res) => {
        if (res.meta.requestStatus === "rejected") {
          setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        } else {
          setLoading(false)
          setShowModal(true)
        }
      })
    } else {
      setLoadingText(
        `${t("Loading")} ${t("Detail")} ${t("Data")} ${t(pageTitle)}`
      )
      setLoading(true)
      dispatch(setDataById(rowData)).then(() => {
        if (storeApp.data.length === 0 && !storeApp.isLoading) {
          dispatch(getDataListApp({})).then((res) => {
            setLoading(false)
            setShowModal(true)
          })
        } else {
          setLoading(false)
          setShowModal(true)
        }
      })
    }
  }

  const handleCloseModal = () => {
    setSidebarOpen(!sidebarOpen)
    setShowModal(!showModal)
    revivalBack()
  }

  const toggleSidebar = () => {
    // console.log("here")
    setFormState("add")
    setSidebarOpen(!sidebarOpen)
    setShowModal(!showModal)
    neutralizeBack(handleCloseModal)
  }

  const handleDelete = async (id) => {
    const result = await MySwal.fire({
      title: `${t("Are you sure?")}`,
      text: `${t("You won't be able to revert")} ${t(pageTitle)}!`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: `${t("Yes, Delete")} ${t(pageTitle)}!`,
      cancelButtonText: `${t("Cancel")}`,
      customClass: {
        confirmButton: "btn btn-primary",
        cancelButton: "btn btn-outline-danger ms-1"
      },
      buttonsStyling: false
    })
    if (result.value) {
      setLoadingText(`${t("Deleting")} Data ${t(pageTitle)}`)
      setLoading(true)
      dispatch(deleteData(id)).then((res) => {
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          setLoading(false)
          toast.success(`${t("Successfully deleted")} ${t(pageTitle)}`, {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          })
        } else if (res.meta.requestStatus === "rejected") {
          setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    }
  }

  const handleDeleteSelected = async () => {
    const countSelectedCopy = selectedRows.length
    const result = await MySwal.fire({
      title: `${t("Are you sure?")}`,
      text: `${t("You will delete")} ${countSelectedCopy} ${t(
        "selected data. Deleted data cannot be recovered"
      )}!`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: `${t("Yes, Delete")} ${countSelectedCopy} ${t(
        pageTitle
      )}!`,
      cancelButtonText: `${t("Cancel")}`,
      customClass: {
        confirmButton: "btn btn-primary",
        cancelButton: "btn btn-outline-danger ms-1"
      },
      buttonsStyling: false
    })
    if (result.value) {
      setLoadingText(
        `${t("Deleting")} ${countSelectedCopy} Data ${t(pageTitle)}`
      )
      setLoading(true)
      const values = selectedRows.map((a) => a.id)
      // console.log(values)
      dispatch(deleteSelectedRowData({ id: values })).then((res) => {
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          setSelectedRows([])
          setToggleCleared(!toggleCleared)
          setLoading(false)
          toast.success(
            `${t("Successfully deleted")} ${countSelectedCopy} ${t(pageTitle)}`,
            {
              style: {
                padding: "16px",
                color: colors.success.main,
                border: `1px solid ${colors.success.main}`
              },
              iconTheme: {
                primary: colors.success.main
              }
            }
          )
        } else if (res.meta.requestStatus === "rejected") {
          setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    }
  }

  /////PAGINATION SERVERSIDE
  const handlePagination = (page) => {
    dispatch(
      getDataList({
        sort_dir,
        sort,
        search: searchTerm,
        length: rowsPerPage,
        page: page.selected + 1,
        ...filtersCustom
      })
    )
    setCurrentPage(page.selected + 1)
  }

  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value)
    dispatch(
      getDataList({
        sort_dir,
        sort,
        search: searchTerm,
        length: value,
        page: currentPage,
        ...filtersCustom
      })
    )
    setRowsPerPage(value)
  }

  const handleDebounceFn = (inputValue) => {
    dispatch(
      getDataList({
        sort_dir,
        search: inputValue,
        sort,
        page: currentPage,
        length: rowsPerPage,
        ...filtersCustom
      })
    )
  }

  const debounceFn = useCallback(debounce(handleDebounceFn, 500), [])

  const handleFilter = (event) => {
    setSearchTerm(event)
    debounceFn(event)
  }

  const handleChangeLookupMode = (
    e,
    id,
    lookupMode_fieldName,
    lookupMode_isMulti
  ) => {
    return lookupMode_onChange(id, lookupMode_fieldName, lookupMode_isMulti)
  }

  const handleFiltersChange = (value, par_name) => {
    filtersCustom[par_name] = value
    dispatch(
      getDataList({
        sort_dir,
        sort,
        search: searchTerm,
        page: currentPage,
        length: rowsPerPage,
        ...filtersCustom
      })
    )
  }

  const handleSort = (column, sortDirection) => {
    setSortDir(sortDirection)
    setSort(column.sortField)
    dispatch(
      getDataList({
        sort_dir,
        sort,
        search: searchTerm,
        page: currentPage,
        length: rowsPerPage,
        ...filtersCustom
      })
    )
  }

  const handleRowSelected = useCallback((state) => {
    setSelectedRows(state.selectedRows)
    if (lookupMode && lookupMode_isMulti) {
      lookupMode_onChange(
        state.selectedRows.map((s) => s.id),
        lookupMode_fieldName,
        lookupMode_isMulti
      )
    }
  }, [])

  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={""}
        nextLabel={""}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={"page-item"}
        nextLinkClassName={"page-link"}
        nextClassName={"page-item next"}
        previousClassName={"page-item prev"}
        previousLinkClassName={"page-link"}
        pageLinkClassName={"page-link"}
        containerClassName={
          "pagination react-paginate justify-content-end my-2 pe-1"
        }
      />
    )
  }

  const renderCell = (row, column) => {
    const field = row[column.fieldName]
    const dataFormat = column.dataFormat
    if (dataFormat) {
      if (dataFormat.includes(":")) {
        const columnTypeFormat = dataFormat.split(":")
        const columnType = columnTypeFormat[0]
        const columnFormat = columnTypeFormat[1]
        let columnFormated = row[column.fieldName]
        if (columnType === "date") {
          columnFormated = Moment(field).format("DD MMM YYYY")
          if (columnFormat) {
            columnFormated = Moment(field).format(columnFormat)
          }
        }
        if (columnType === "datetime") {
          columnFormated = Moment(field).format("DD MMM YYYY HH:mm")
          if (columnFormat) {
            columnFormated = Moment(field).format(columnFormat)
          }
        }
        return columnFormated
      }
    }
    return row[column.fieldName]
  }

  const columns = [
    ...store.dynamicColumns.map((column) => ({
      name: column.columnName,
      sortField: column.fieldName,
      center: false,
      sortable: true,
      minWidth: column.minWidth,
      selector: (row) => row[column.fieldName],
      cell: (row) => renderCell(row, column)
    })),
    {
      name: t("App"),
      center: false,
      sortable: false,
      selector: (row) => row?.app_logo,
      cell: (row) => (
        <div className="d-flex justify-content-left align-items-center">
          {row?.app_logo ? (
            <Avatar
              className="me-1"
              img={row?.app_logo}
              width="32"
              height="32"
              imgHeight="25"
              imgWidth="30"
              color={"light-primary"}
            />
          ) : (
            <Avatar
              initials
              className="me-1"
              // color={row.avatarColor}
              content={row?.app_name ?? ""}
            />
          )}

          <div className="d-flex flex-column">
            <span className="fw-bolder">{row.app_name}</span>
          </div>
        </div>
      )
    },
    // {
    //   name: t("App Name"),
    //   sortField: "attributes",
    //   center: false,
    //   sortable: true,
    //   selector: (row) => row.attributes,
    //   cell: (row) => row.attributes
    // },
    !lookupMode && {
      name: t("Actions"),
      center: true,
      width: "140px",
      cell: (row) => (
        <GlobalActionsTable
          disabledAction={disabledAction}
          row={row}
          handleDelete={handleDelete}
          handleEdit={handleEdit}
        />
      )
      // (
      //   <Input
      //     disabled={row.hidden}
      //     defaultChecked={row.checked}
      //     onChange={(e) =>
      //       handleChangeLookupMode(
      //         e,
      //         row.id,
      //         lookupMode_fieldName,
      //         lookupMode_isMulti
      //       )
      //     }
      //     type="checkbox"
      //     id={`checkbox-${row.id}`}
      //   />
      // )
    }
  ]

  const tableColumns = useMemo(
    () =>
      store.isLoading
        ? columns.map((column) => ({
            name: t(column.name),
            ...column,
            sortable: false,
            cell: (row) =>
              column.name === "Actions" ? (
                <Fragment>
                  <Skeleton
                    sx={{ bgcolor: colors.dark.light }}
                    animation="wave"
                    className="ms-2 me-1"
                    width={30}
                    height={40}
                  />
                  <Skeleton
                    sx={{ bgcolor: colors.dark.light }}
                    animation="wave"
                    width={30}
                    height={40}
                  />
                </Fragment>
              ) : (
                <Skeleton
                  sx={{ bgcolor: colors.dark.light }}
                  animation="wave"
                  width="100%"
                  height={40}
                />
              )
          }))
        : columns.map((column) => ({
            name: t(column.name),
            ...column
          })),
    [store.isLoading, columns]
  )

  const dataToRender = () => {
    const filters = {
      search: searchTerm
    }
    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    if (!store.isLoading) {
      if (store.data.length > 0) {
        return store.data
      } else if (store.data.length === 0 && isFiltered) {
        return []
      } else {
        // console.log(store.allData)
        // return store.allData.slice(0, rowsPerPage)
      }
    } else {
      return Array(10).fill({})
    }
  }

  const removeDefaultValue = (data, dataTree) => {
    // console.log(dataTree)
    let mainArray = JSON.parse(JSON.stringify(dataTree))
    // mainArray.forEach(function iter(a) {
    //   if (data.includes(a.id)) {
    //     a["checked"] = true
    //     a["hidden"] = true
    //   }
    //   // console.log(a.id, !data.includes(a.id))

    //   // eslint-disable-next-line no-unused-expressions
    //   Array.isArray(a.children) && a.children.forEach(iter)
    // })
    // dispatch(setDataList(mainArray))
  }

  useEffect(() => {
    if (store.data.length === 0 || (!store.isLoading && getAlwaysGrid)) {
      setLoadingText(`${t("Loading Data")} ${t(pageTitle)}`)
      dispatch(
        getDataList({
          sort_dir,
          sort,
          search: searchTerm,
          page: currentPage,
          length: rowsPerPage,
          ...filtersCustom
        })
      ).then((x) => {
        if (lookupMode && lookupMode_isM2MDefValFromGrid) {
          removeDefaultValue(lookupMode_defaultValue, x.payload.data)
        }
      })
    }
    if (store.data.length > 0 && lookupMode_isM2MDefValFromGrid && lookupMode) {
      removeDefaultValue(lookupMode_defaultValue, store.data)
    }
  }, [dispatch, sort_dir, sort, currentPage, filtersCustom])

  return (
    <Fragment>
      <UILoader blocking={loading} loader={<Loader />}>
        <Card className="overflow-hidden">
          <div className="react-dataTable react-dataTable-selectable-rows">
            <DataTable
              noHeader
              subHeader
              sortServer
              responsive
              pagination
              paginationServer
              paginationTotalRows={store.total}
              paginationComponent={CustomPagination}
              selectableRows
              onSelectedRowsChange={handleRowSelected}
              selectableRowsHighlight
              // onRowClicked={handleRowSelected}
              clearSelectedRows={toggleCleared}
              selectableRowsComponent={BootstrapCheckbox}
              noDataComponent={<NoDataDisplay />}
              // fixedHeader
              // fixedHeaderScrollHeight="600px"
              style={{ "min-height": "400px" }}
              highlightOnHover
              pointerOnHover
              columns={tableColumns}
              onSort={handleSort}
              sortIcon={<ChevronDown />}
              className="react-dataTable"
              data={dataToRender()}
              subHeaderComponent={
                <CustomHeader
                  store={store}
                  searchTerm={searchTerm}
                  rowsPerPage={rowsPerPage}
                  handleFilter={handleFilter}
                  handlePerPage={handlePerPage}
                  toggleSidebar={toggleSidebar}
                  selectedRow={selectedRows}
                  handleDeleteSelected={handleDeleteSelected}
                  handleFiltersChange={handleFiltersChange}
                  filtersCustomAdd={filtersCustomAdd}
                  lookupMode={lookupMode}
                />
              }
            />
          </div>
        </Card>

        {/* <Sidebar open={sidebarOpen} toggleSidebar={toggleSidebar} /> */}
      </UILoader>

      {showModal && (
        <ModalAddEdit
          formState={formState}
          show={showModal}
          setShow={toggleSidebar}
          detailPage={false}
          setFormState={setFormState}
        />
      )}
    </Fragment>
  )
}

export default List
